<template>
	<div>
		<b-input-group>
			<b-input-group-prepend>
				<b-button variant="primary" 
						  :size="size"
						  :disabled ="d_value <= min" 
						  @click="onMinusHandler">
					-
				</b-button>
			</b-input-group-prepend>

			<b-form-input class="text-center"
						  type="number" 
						  :size="size"
						  lazy-formatter
						  :formatter="valueFormatter"
						  v-model="d_value"
						  @change="onInputHandler">
			</b-form-input>

			<b-input-group-prepend>
				<b-button variant="primary" 
						  :size="size"
						  :disabled ="d_value >= max" 
						  @click="onAddHandler">
					+
				</b-button>
			</b-input-group-prepend>
		</b-input-group>
	</div>
</template>


<script>
export default {
	name: 'CounterWidget',
	data: function() {
		return {
			d_value: 0
		}
	},
	props: {
		max: {
			type: Number,
			default: 100,
		},
		min: {
			type: Number,
			default: 0,
		},
		value: {
			type: Number,
			default: 0,
		},
		size: {
			type: String,
			default: "sm",
		}
	},
	mounted: function() {
		this.d_value = Number(this.value);
	},
	methods: {
		onAddHandler: function() {
			this.d_value += 1;
			this.$emit('input', this.d_value);
		},
		onMinusHandler: function() {
			this.d_value -= 1;
			this.$emit('input', this.d_value);
		},
		valueFormatter: function(value) {
			if (value === "") {
				return 0;
			}
			value = Number(value);
			if (value > this.max) {
				value = this.max;
			}
			else if (value < this.min) {
				value = this.min;
			}
			return value;
		},
		onInputHandler: function(value) {
			this.$emit('input', this.valueFormatter(value));
		},
	},
}
</script>
